import { Outlet } from 'react-router-dom'
import { useLocation } from 'react-router-dom'

import Navigation from '../components/navbarComponent/Navigation'
import MainCmp from '../components/mainComponent/Main'
import Footer from '../components/footerComponent/Footer'
import HeroSection from '../components/heroComponent/HeroSection'
import Hero from '../components/heroComponent/Hero'
import { useDispatch, useSelector } from 'react-redux'
import { heroActions } from '../store/hero-slice'
import React from 'react'
import SvgCmp from '../components/svgComponent/SvgCmp'

const Root = () => {
	const location = useLocation()

	// #region --> handling full screen picture
	const dispatch = useDispatch()
	const carouselData = useSelector((state: any) => state.hero.carouselData)
	const handleCloseFullScreen = () => {
		dispatch(heroActions.updateCarouselData({ showFullScreen: false, img: '' }))
		document.body.classList.remove('overflow-hidden') // Remove the class to allow scrolling
	}

	const fullScreenImg = (
		<div
			id='fullscreenImg'
			className='fixed  w-full h-full bg-black/90 z-[50] flex justify-center items-center cursor-pointer'
			onClick={handleCloseFullScreen}>
			<div className='mx-2 xl:mx-40 my-auto object-contain'>
					<SvgCmp
						svgName={'plus'}
						type={'galleryClose'}
						styles='absolute top-2 xl:top-10 right-2 xl:right-10 m-4 h-10 w-10 xl:h-16 xl:w-16 cursor-pointer rotate-45'				
					/>
				<img
					className=' block max-w-full h-auto my-auto mx-auto shadow-sm cursor-pointer'
					src={carouselData.img}
					alt='Full Screen'
				/>
			</div>
		</div>
	)
	// #endregion <--handling full screen picture

	return (
		<React.Fragment>
			<div className='text-green-900 relative flex flex-col min-h-screen'>
			{carouselData.showFullScreen && fullScreenImg}
				{!carouselData.showFullScreen && <Navigation />}
				<main
					id='maincmp'
					className='bg-custom-color flex-1'>
					<MainCmp>
						{(location.pathname === '/ZZiR' || location.pathname === '/ZZiR/ZieloneDachy') && (
							<HeroSection children={<Hero />} />
						)}
						<Outlet />
					</MainCmp>
				</main>
				<Footer />
			</div>
		</React.Fragment>
	)
}

export default Root
