const Project = (props: any) => {
	console.log(1);
	const [currentIndex, setCurrentIndex] = useState(0)

	const imgRefs = useRef<(HTMLDivElement | null)[]>([]);
	const [isVisible, setIsVisible] = useState(false)
	const ref = useRef<HTMLDivElement>(null)

	CheckIfVisible(ref, setIsVisible)

// #region --> Scrollowanie do aktywnego elementu

	const scrollContainerRef = useRef<HTMLDivElement>(null)
	const itemRefs = useRef<(HTMLDivElement | null)[]>([])
	useEffect(() => {
		console.log(2);
		if (isVisible) itemRefs.current = itemRefs.current.slice(0, props.data.imges.length)
	}, [isVisible])
	const handleScroll = () => {
		if (isVisible) {
			const scrollLeft = scrollContainerRef.current!.scrollLeft
			// let closest = props.data.imges[0]
			let closestDistance = Infinity

			props.data.imges.forEach((img: { id: string }) => {
				const element = document.getElementById(img.id)
				if (!element) return
				const rect = element.getBoundingClientRect()
				const distance = Math.abs(rect.left - scrollLeft)
				console.log('distance', distance)
				if (distance < closestDistance) {
					closestDistance = distance
				}
			})
		}
	}

	useEffect(() => {
		console.log(3);
		const scrollContainer = scrollContainerRef.current
		if (!scrollContainer) return

		const observer = new IntersectionObserver(([entry]) => {
			if (entry.isIntersecting) {
				scrollContainer.addEventListener('scroll', handleScroll)
			} else {
				scrollContainer.removeEventListener('scroll', handleScroll)
			}
		})
		observer.observe(scrollContainer)
		return () => {
			observer.unobserve(scrollContainer)
		}
	}, [])

// #endregion <-- Scrollowanie do aktywnego elementu

	// useEffect(() => {
	// 	setCurrentIndex(carosuelMobileData.index)
	// 	imgRefs.current[carosuelMobileData.index]?.scrollIntoView({ behavior: 'smooth', block: 'nearest', inline: 'start' });
	// },[carosuelMobileData.index])



	// #region --> Working code
	const handdleImgCLick = (event: any) => {
		console.log('event: ', event)
		setCurrentIndex(event)
		imgRefs.current[event]?.scrollIntoView({ behavior: 'smooth', block: 'nearest', inline: 'start' });
	}

	const desktopMiniGallery = (
		<div className='col-span-5 grid grid-cols-3 gap-8 lg:ml-5'>
			{props.data.imges.map((img: any, index: number) => (
				<img
					src={img}
					alt=''
					className={`lg:h-[7rem] lg:w-[8.6875rem] lg:rounded-3xl cursor-pointer lg:hover:shadow-xl ${index === currentIndex ? 'border-2 border-green-900' : ''}`}
					onClick={() => handdleImgCLick(props.data.imges.indexOf(img))}
				/>
			))}
		</div>
	)

				console.log('currentIndex', currentIndex);

	const mobileGallery = props.data.imges.map((img: any, index: number) => (
		<div ref={el => imgRefs.current[index] = el} className='relative inline-block m-2 bg-slate-500 rounded-xl overflow-hidden'>
			<div className={`h-[7.5rem] w-[7.5rem] object-fill`}>
				<img
					src={img}
					alt='test'
					className='object-cover h-full w-full'
					onClick={() => handdleImgCLick(index)}
					// onClick={() => handdleImgCLick(props.data.imges.indexOf(img))}
				/>
			</div>
		</div>
	))

	const mobileMiniGallery = (
		<div  className='block w-full overflow-hidden'>
			<div className='block w-full overflow-x-scroll whitespace-nowrap'>{mobileGallery}</div>
		</div>
	)
	// #endregion <--Working code
	
	
	
	return (
		<div className='mx-4 xl:mx-10 xl:grid max-md:pt-8'>
			<div className=''>
				<p className='text-base xl:text-2xl font-semibold xl:leading-normal mb-4 xl:mb-6'>{props.data.title}</p>
				<p className='text-sm xl:text-base font-normal xl:leading-normal xl:text-gray-800 mb-6 xl:mb-8'>
					{props.data.description}
				</p>
			</div>
			<div className='grid grid-cols-12'>
				<div className='col-span-12 xl:col-span-7 max-md:mb-6 lg:cursor-pointer'>
					<Carousel
						images={props.data.imges}
						index={currentIndex}
						setCurrentIndex={handdleImgCLick}
					/>
				</div>
				{window.innerWidth > 1024 && desktopMiniGallery}
			</div>
			{window.innerWidth <= 1024 && mobileMiniGallery}
		</div>
	)
}

export default Project
