// let isMobile
const Offers = () => {
	const [activeItem, setActiveItem] = useState<any>(offerList[0])
	const [isVisible, setIsVisible] = useState(false)
	const ref = useRef<HTMLDivElement>(null)
	CheckIfVisible(ref, setIsVisible)

	//#region --> handling scroll to left side
	const scrollContainerRef = useRef<HTMLDivElement>(null)
	const itemRefs = useRef<(HTMLDivElement | null)[]>([])
	useEffect(() => {
		if (isVisible) itemRefs.current = itemRefs.current.slice(0, offerList.length)
	}, [isVisible])
	const handleScroll = () => {
		if (isVisible) {
			const scrollLeft = scrollContainerRef.current!.scrollLeft
			let closest = offerList[0]
			let closestDistance = Infinity

			offerList.forEach(offer => {
				const element = document.getElementById(offer.id)
				if (!element) return
				const rect = element.getBoundingClientRect()
				const distance = Math.abs(rect.left - scrollLeft)
				console.log('distance', distance)
				if (distance < closestDistance) {
					closest = offer
					closestDistance = distance
				}
			})
			setActiveItem(closest)
		}
	}

	useEffect(() => {
		const scrollContainer = scrollContainerRef.current
		if (!scrollContainer) return

		const observer = new IntersectionObserver(([entry]) => {
			if (entry.isIntersecting) {
				scrollContainer.addEventListener('scroll', handleScroll)
			} else {
				scrollContainer.removeEventListener('scroll', handleScroll)
			}
		})
		observer.observe(scrollContainer)
		return () => {
			observer.unobserve(scrollContainer)
		}
	}, [])

	//#endregion

	const handleCardClick = (event: any) => {
		setActiveItem(offerList.find(x => x.id === event.currentTarget.id))
	}

	//#region --> MOBILE
	const contentOfferMobileList = offerList.map((offer, index) => (
		<OfferMobileItem
			key={offer.id}
			id={offer.id}
			img={offer.img}
			title={offer.title}
			text={offer.text}
			background={offer.background}
			customClickEvent={handleCardClick}
			isActive={activeItem.id === offer.id}
			ref={el => (itemRefs.current[index] = el)}
			setActiveItem={setActiveItem}
		/>
	))

	const mobile = (
		<div
			ref={scrollContainerRef}
			className='mt-6 bg-green-900'>
			<div className='max-md:block max-md:w-full max-md:overflow-hidden pt-8 pb-8'>
				<div className='flex w-full overflow-x-scroll whitespace-nowrap'>{contentOfferMobileList}</div>
			</div>
			<div className='col-span-4'>
				<OfferInfo
					key={activeItem.id}
					isActive={activeItem.page}
					itemsAmount={offerList.length}
					title={activeItem.title}
					text={activeItem.text}
				/>
			</div>
		</div>
	)
	//#endregion

	//#region --> DESKTOP
	const contentOfferList = offerList.map(offer => (
		<OfferItem
			key={offer.id}
			id={offer.id}
			img={offer.img}
			title={offer.title}
			text={offer.text}
			background={offer.background}
			customClickEvent={handleCardClick}
			isActive={activeItem.id === offer.id}
		/>
	))
	const desktop = (
		<div className='lg:mt-12 bg-green-900 lg:rounded-2xl relative z-10'>
			<div className='lg:p-12 lg:grid lg:grid-cols-12'>
				<div className='lg:col-span-8 lg:grid lg:grid-cols-4 lg:gap-4'>{contentOfferList}</div>
				<div className='col-span-4'>
					<OfferInfo
						key={activeItem.id}
						isActive={activeItem.page}
						itemsAmount={offerList.length}
						title={activeItem.title}
						text={activeItem.text}
						length={offerList.length}
					/>
				</div>
			</div>
		</div>
	)
	//#endregion

	return <>{window.innerWidth < 768 ? mobile : desktop}</>
}

export default Offers
