import React, { useEffect, useState, useRef } from 'react'
import CheckIfVisible from '../../../../hooks/CheckIfVisible'

let status = true
const Stat = (props: any) => {
	const { title, number, duration, prefix } = props.data
	const [count, setCount] = useState('0')
	const [isVisible, setIsVisible] = useState(false)
	const ref = useRef<HTMLDivElement>(null)
	CheckIfVisible(ref, setIsVisible, true)
	

	//#region Counting
	// number displayed by component
	useEffect(() => {
		if (isVisible) {
			let start = 0
			// first three numbers from props
			const end = parseInt(number.substring(0, 3))
			// if zero, return
			if (start === end) return

			// find duration per increment
			let totalMilSecDur = parseInt(duration)
			let incrementTime = (totalMilSecDur / end) * 1000

			// timer increments start counter
			// then updates count
			// ends if start reaches end
			let timer = setInterval(() => {
				start += 1
				setCount(String(start) + number.substring(3))
				if (start === end) clearInterval(timer)
			}, incrementTime)
		}

		// dependency array
	}, [number, duration, isVisible])

	//#endregion
	return (
		<div
			ref={ref}
			className='lg:text-center col-span-12 lg:col-span-6 max-sm:flex'>
			<p className='text-2xl lg:text-4xl font-bold lg:font-semibold'>
				{count.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ' ') + prefix}
			</p>
			<p className='max-sm:ml-2 mt-1 lg:mt-2 text-base lg:text-xl text-gray-800 font-medium lg:font-semibold'>
				{title}
			</p>
		</div>
	)
}

export default Stat
