import { useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { heroActions } from '../../store/hero-slice'

import Card from './Card'
import cardImg1 from '../../img/pages/Header/ZielenMiejska.jpg'
import cardImg2 from '../../img/pages/Header/ZieloneDachy.jpeg'

const cardInfo = [
	{
		id: 'plants',
		img: cardImg1,
		alt: 'Krople wody spadajce na roliny',
		title: 'Ziele miejska',
		text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam.',
		inActive: '<- Ziele miejska',
		inActiveMobile: 'Ziele miejska ->',
	},
	{
		id: 'roofs',
		img: cardImg2,
		alt: 'Zielone dachy widziane z lotu ptaka',
		title: 'Zielone dachy',
		text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam.',
		inActive: 'Zielone dachy ->',
		inActiveMobile: 'Zielone dachy ->',
	},
]

const HeroCards = (props: any) => {
	const dispatch = useDispatch()
	const heroState = useSelector((state: any) => state.hero)
	const navigate = useNavigate()

	const handleClickCard = (cardName: string) => {
		switch (cardName) {
			case 'plants': {
				dispatch(heroActions.clickCards({ type: 'plants', nav: '/ZZiR' }))
				navigate('/ZZiR')
				// window.scrollTo({ top: 0 })
				break
			}
			case 'roofs': {
				dispatch(heroActions.clickCards({ type: 'roofs', nav: '/ZZiR/ZieloneDachy' }))
				navigate('/ZZiR/ZieloneDachy')
				// window.scrollTo({ top: 0 })
				break
			}
		}
	}

	return (
		<div className='flex max-sm:flex-col mt-8 xl:mt-16 relative'>
			<div className={`xl:mr-4 transition-all duration-400 ${heroState.isPlantsActive ? 'xl:w-8/12' : 'xl:w-4/12'}`}>
				<Card
					data={cardInfo[0]}
					status={heroState.isPlantsActive}
					customClickEvent={() => handleClickCard('plants')}
				/>
			</div>
			<div className={`xl:ml-4 transition-all duration-400 ${heroState.isRoofsActive ? 'xl:w-8/12' : 'xl:w-4/12'}`}>
				<Card
					data={cardInfo[1]}
					status={heroState.isRoofsActive}
					customClickEvent={() => handleClickCard('roofs')}
				/>
			</div>
		</div>
	)
}

export default HeroCards
