const activeCardStyling =
	'max-w-full bg-green-900 border border-none rounded-xl xl:rounded-3xl transition-shadow hover:shadow-2xl hover:bg-green-800 relative max-md:mt-4'

const defaultCardStyling =
	'max-w-full bg-hero-inactive border border-none rounded-xl xl:rounded-3xl transition-shadow hover:shadow-2xl hover:bg-gray-50 relative max-md:mt-4'


const Card = (props: any) => {
	const activeCardText = (
		<div className='px-6 pb-6'>
			<h5
				className={`mb-[10px] text-sm xl:text-base font-semibold tracking-tight ${
					props.status ? 'text-gray-50' : ' text-green-900'
				}`}>
				{props.data.title}
			</h5>
			<p className={`text-sm font-medium leading-normal text-gray-200`}>{props.data.text}</p>
		</div>
	)
	const inActiveCardText = (
		<div className={`px-6 py-4 xl:pb-6 xl:pt-8 max-sm:w-full`}>
			<p className='text-green-900 font-semibold text-2xl xl:text-4xl leading-normal xl:self-strech xl:mb-1'>
				{window.innerWidth > 1024 ? props.data.inActive : props.data.inActiveMobile}
			</p>
		</div>
	)

	return (
		<div
			className={`${props.status ? activeCardStyling : defaultCardStyling }`}
			onClick={props.customClickEvent}
			role='button'>
			<img
				className={`rounded-3xl p-4 h-[188px] xl:h-80 w-full object-cover transition-opacity duration-500 delay-75 ${!props.status ? 'max-sm:hidden max-md:opacity-0' : 'max-md:opacity-100'}`}
				src={props.data.img}
				alt={props.data.alt}
			/>
			{props.status ? activeCardText : inActiveCardText}
		</div>
	)
}

export default Card
