import React, { useEffect } from 'react'

import { useState } from 'react'

import SvgCmp from '../../components/svgComponent/SvgCmp'
import { useDispatch, useSelector } from 'react-redux'
import { heroActions } from '../../store/hero-slice'

interface CarouselProps {
	images: string[]
	index: number
	setCurrentIndex: (index: number) => void // dodanie do listy propsw
}

const Carousel = (props: CarouselProps) => {
	const [currentIndex, setCurrentIndex] = useState(0)
	const dispatch = useDispatch()
	const [touchStart, setTouchStart] = useState(0)
	const [touchEnd, setTouchEnd] = useState(0)

	useEffect(() => {
		setCurrentIndex(props.index)
	}, [props.index])

	//#region --> handling finger slide
	const handleTouchStart = (event: React.TouchEvent) => {
		setTouchStart(event.touches[0].clientX)
	}

	const handleTouchMove = (event: React.TouchEvent) => {
		setTouchEnd(event.touches[0].clientX)
	}
	const handleTouchEnd = () => {
		if (touchEnd < touchStart) {
			handleNext()
		}

		if (touchEnd > touchStart) {
			handlePrevious()
		}
	}
	// #endregion <--handling finger slide

	// #region --> handling arrow events
	const handleNext = () => {
		const newIndex = currentIndex + 1 === props.images.length ? 0 : currentIndex + 1
		setCurrentIndex(prevIndex => (prevIndex + 1 === props.images.length ? 0 : prevIndex + 1))
		props.setCurrentIndex(newIndex)
	}
	const handlePrevious = () => {
		const newIndex = currentIndex - 1 < 0 ? props.images.length - 1 : currentIndex - 1
		setCurrentIndex(prevIndex => (prevIndex - 1 < 0 ? props.images.length - 1 : prevIndex - 1))
		props.setCurrentIndex(newIndex)
	}
	const handleDotClick = (index: any) => {
		setCurrentIndex(index)
	}
	// #endregion <--handling arrow events

	// #region --> handling full screen picture
	const handleImageClick = () => {
		dispatch(
			heroActions.updateCarouselData({
				showFullScreen: true,
				img: props.images[currentIndex],
			})
		)
		document.body.classList.add('overflow-hidden') // Add the class to prevent scrolling
	}
	// #endregion <--handling full screen picture

	const arrowButtonClasses =
		'bg-green-700 py-2 px-3 mx-4 rounded-lg absolute top-0 bottom-0 my-auto max-md:mb-4 h-10 w-10 xl:cursor-pointer hover:bg-green-800 focus:outline-none'

	return (
		<React.Fragment>
			<div className=' relative overflow-hidden w-full xl:w-[687px] h-80 xl:h-[25rem] rounded-xl xl:rounded-3xl'>
				<img
					key={currentIndex}
					src={props.images[currentIndex]}
					onClick={handleImageClick}
					onTouchStart={handleTouchStart}
					onTouchMove={handleTouchMove}
					onTouchEnd={handleTouchEnd}
					alt=''
					className='h-full'
				/>
				<div className='slide_direction flex justify-between'>
					<div
						className={`${arrowButtonClasses} left-0`}
						onClick={handlePrevious}>
						<SvgCmp
							svgName={'arrowLeft'}
							type={'gallery'}
						/>
					</div>
					<div
						className={`${arrowButtonClasses} right-0`}
						onClick={handleNext}>
						<SvgCmp
							svgName={'arrowRight'}
							type={'gallery'}
						/>
					</div>
				</div>
			</div>
		</React.Fragment>
	)
}

export default Carousel
