import { ReactNode } from 'react'
import FadeInUpHook from '../../hooks/FadeInUpHook'

interface SectionCmpProps {
	children: ReactNode
	text?: string
}

const styling = 'container mx-auto max-w-screen-xl relative py-5 xl:py-16 scrollbar-hide'


const SectionCmp: React.FC<SectionCmpProps> = ({ children, text }) => {
	const { ref, fadeInUpAnimation } = FadeInUpHook()
	return <div ref={ref} className={`${styling} ${text && text} ${fadeInUpAnimation}`}>{children}</div>
}

export default SectionCmp
