import { useState } from 'react'
import FAQItem from './FAQItem'

const faqData = [
	{
		id: 'accordion1',
		num: 1,
		title: 'Dlaczego warto wybra rozchodniki?',
		content:
			'Dziki pytkiemu systemowi korzeniowemu rozchodnikowi wystarcza mniejsza warstwa podoa. Jako rolina odporna na susz, choroby oraz szkodniki nie wymaga wielu skadnikw odywczych, ani zabiegw pielgnacyjnych.',
	},
	{
		id: 'accordion2',
		num: 2,
		title: 'Jakie s zalety zielonych dachw?',
		content:
			'Zielone dachy s nie tylko estetyczne, ale rwnie przynosz wiele korzyci. Dziki nim mona zaoszczdzi na kosztach ogrzewania i klimatyzacji, a take poprawi jako powietrza w miecie.',
	},
	{
		id: 'accordion3',
		num: 3,
		title: 'Jakie s zalety zielonych dachw?',
		content:
			'Zielone dachy s nie tylko estetyczne, ale rwnie przynosz wiele korzyci. Dziki nim mona zaoszczdzi na kosztach ogrzewania i klimatyzacji, a take poprawi jako powietrza w miecie.',
	},
	{
		id: 'accordion4',
		num: 4,
		title: 'Czy jest zielony dach ekstensywny?',
		content:
			'Zielone dachy s nie tylko estetyczne, ale rwnie przynosz wiele korzyci. Dziki nim mona zaoszczdzi na kosztach ogrzewania i klimatyzacji, a take poprawi jako powietrza w miecie.',
	},
]

const FAQAccordion = () => {
	const [activeId, setActiveId] = useState<string | null>(null);

	const handleClick = (id: string) => {
		setActiveId(prevId => (prevId === id ? null : id));
	  };
	

	const faqList = faqData.map(item => (
		<FAQItem
			key={item.id}
			item={item}
			isActive={item.id === activeId}
			onClick={() => handleClick(item.id)}
		/>
	))

	return (
		
		<div
			id='accordion-color'
			data-accordion='collapse'
			data-active-classNamees='bg-blue-100 dark:bg-gray-800 text-blue-600 dark:text-white'>
			{faqList}
		</div>
	)
}

export default FAQAccordion
