import { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { motion } from 'framer-motion'

import PrimaryButton from '../buttonComponent/PrimaryButton'
import SecondaryButton from '../buttonComponent/SecondaryButton'
import React from 'react'

const getWindowSize = () => {
	const { innerWidth, innerHeight } = window
	return { innerWidth, innerHeight }
}

const HeroHeading = () => {
	const y = 100
	const duration = 0.5
	const [windowSize, setWindowSize] = useState(getWindowSize())
	const heroState = useSelector((state: any) => state.hero)

	useEffect(() => {
		function handleWindowResize() {
			setWindowSize(getWindowSize())
		}

		window.addEventListener('resize', handleWindowResize)

		return () => {
			window.removeEventListener('resize', handleWindowResize)
		}
	}, [])

	const desktopHeaderTitle = (
		<React.Fragment>
			<div className='overflow-hidden'>
				<motion.div
					initial={{ opacity: 0, y: y }}
					animate={{ opacity: 1, y: 0 }}
					transition={{ duration: duration, type: 'tween' }}>
					Tworzymy <span className='text-green-600'>pikne ogrody</span> i <span className='text-green-600'> zielone</span>
				</motion.div>
			</div>
			<div className='overflow-hidden'>
				<motion.div
					initial={{ opacity: 0, y: y }}
					animate={{ opacity: 1, y: 0 }}
					transition={{ duration: duration, type: 'tween' }}>
					<span className='text-green-600'> dachy</span> na kadej powierzchni&nbsp;
					<span className={windowSize.innerWidth > 1200 ? 'hero-svg' : 'hero-svg--mobile'}></span>
				</motion.div>
			</div>
		</React.Fragment>
	)
	const mobileHeaderTitle = (	<React.Fragment>
		<div className='overflow-hidden'>
			<motion.div
				initial={{ opacity: 0, y: 100 }}
				animate={{ opacity: 1, y: 0 }}
				transition={{ duration: duration, type: 'tween' }}>
				Tworzymy <span className='text-green-600'>pikne ogrody</span> i
			</motion.div>
		</div>
		<div className='overflow-hidden'>
			<motion.div
				initial={{ opacity: 0, y: 100 }}
				animate={{ opacity: 1, y: 0 }}
				transition={{ duration: duration, type: 'tween' }}>
				<span className='text-green-600'>zielone dachy</span> na kadej
			</motion.div>
		</div>
		<div className='overflow-hidden'>
			<motion.div
				initial={{ opacity: 0, y: 100 }}
				animate={{ opacity: 1, y: 0 }}
				transition={{ duration: duration, type: 'tween' }}>
				powierzchni&nbsp;
				<span className={windowSize.innerWidth > 1200 ? 'hero-svg' : 'hero-svg--mobile'}></span>
			</motion.div>
		</div>
	</React.Fragment>)

	return (
		<div className='grid grid-cols-12 relative'>
			<div className='col-span-12 xl:col-span-6'>
				<h1 className='text-2xl xl:text-4xl text-green-900 font-semibold xl:leading-normal'>
					{windowSize.innerWidth > 1024 ? desktopHeaderTitle : mobileHeaderTitle}
				</h1>
			</div>
			<div className='col-span-12 grid grid-cols-12 xl:col-span-6 xl:ml-8'>
				<div className='col-span-12 max-md:mt-4 xl:col-span-12 overflow-hidden'>
					<motion.p
						initial={{ opacity: 0, y: y }}
						animate={{ opacity: 1, y: 0 }}
						transition={{ duration: duration, type: 'tween' }}
						className='text-sm font-normal xl:text-base text-gray-800'>
						Wsplnie z nami stwrz funkcjonaln i pikn przestrze zielon. Dziki naszemu dowiadczeniu, zdobytemu w
						cigu wielu lat praktyki i szkole, zagwarantujemy Ci najlepsze efekty.
					</motion.p>
				</div>
				<div className='col-span-12 mt-4'>
					<div className='space-x-2 xl:space-x-4 flex '>
						<div className='overflow-hidden'>
							<motion.div
								initial={{ opacity: 0, y: y }}
								animate={{ opacity: 1, y: 0 }}
								transition={{ duration: duration, type: 'tween' }}>
								<PrimaryButton />
								{/* <SecondaryButton /> */}
							</motion.div>
						</div>
						<div className='overflow-hidden '>
							<motion.div
								initial={{ opacity: 0, y: y }}
								animate={{ opacity: 1, y: 0 }}
								transition={{ duration: duration, type: 'tween' }}>
								{heroState.type === 'plants' && <SecondaryButton />}
							</motion.div>
						</div>
					</div>
				</div>
			</div>
		</div>
	)
}
export default HeroHeading
