import image from '../../../styles/gallery/gallery-1.jpeg'

const CheckImg = (param: any) => {
	switch (param) {
		case 1:
			return 'bg-[url("/gallery/gallery-1.jpeg")]'
		case 2:
			return 'bg-[url("/gallery/gallery-2.jpeg")]'
		case 3:
			return 'bg-[url("/gallery/gallery-3.jpeg")]'
		case 4:
			return 'bg-[url("/gallery/gallery-4.jpeg")]'
		case 5:
			return 'bg-[url("/gallery/gallery-5.jpeg")]'
		default:
			return 'error'
	}
}

const GalleryItem = (props: any) => {
	const picture: string = `bg-gallery-${props.img}`
	const image: string = CheckImg(props.img)

	return (
		<div
			className={`relative rounded-3xl w-24 lg:w-56 h-80 lg:h-80 overflow-hidden hover:lg:w-96 hover:lg:shadow-2xl ease-in-out duration-300 flex justify-center items-end hover:lg:scale-105`}>
			<div className={`h-full w-full ${image} bg-center hover:scale-150 transition-all duration-300`}>
				<div className='absolute p-4 h-full text-gray-100 flex flex-col justify-end opacity-0 hover:opacity-100 hover:scale-[.67] bg-gradient-to-t from-gray-900/70 to-gray-900/0 transition-all hover:delay-75 duration-0 hover:duration-300'>
					<p className='mb-2 text-base font-semibold'>TITLE</p>
					<p className='text-sm font-normal'>
						Lorem ipsum, dolor sit amet consectetur adipisicing elit. Nihil, itaque.
					</p>
				</div>
			</div>
		</div>
	)
}

export default GalleryItem

