import React from 'react'

import Stat from './Stat'

const statistics = [
	{
		id: 1,
		number: '75',
		duration: '1',
		prefix: '',
		title: 'Placw zabaw',
	},
	{
		id: 2,
		number: '13765',
		duration: '1,5',
		prefix: ' m',
		title: 'Ogrodw',
	},
	{
		id: 3,
		number: '15225',
		duration: '2',
		prefix: ' m',
		title: 'Zelonych dachw',
	},
	{
		id: 4,
		number: '312',
		duration: '2,5',
		prefix: '',
		title: 'Wygranych przetargw',
	},
]

const Stats = () => {


	const statisticsList = statistics.map(stat => (
		<React.Fragment key={stat.id}>
			<Stat
				key={stat.id}
				data={stat}
				length={statistics.length}
			/>
			{statistics.length !== stat.id && window.innerWidth > 640 && (
				<div
					id={`${stat.id}`}
					className='lg:w-[1px] lg:h-16 lg:bg-separator lg:mt-2 lg:rounded-full'></div>
			)}
			{statistics.length !== stat.id && window.innerWidth <= 640 && (
				<div
					id={`${stat.id}`}
					className='w-[264px] h-[1px] bg-separator rounded-full'></div>
			)}
		</React.Fragment>
	))
	return (
		<div
			id='stats'
			className={`relative mx-4 lg:mx-10 py-8 px-8 lg:px-16 grid grid-cols-12 gap-y-4 lg:flex lg:flex-row lg:justify-between bg-stat-col rounded-2xl lg:rounded-[32px]`}>
			{statisticsList}
		</div>
	)
}

export default Stats
