import PaginationItem from './PaginationItem'
import { useEffect } from 'react'

const list = [
	{
		lol: 1,
	},
	{
		lol: 2,
	},
	{
		lol: 3,
	},
	{
		lol: 4,
	},
	{
		lol: 5,
	},
	{
		lol: 6,
	},
	{
		lol: 7,
	},
	{
		lol: 8,
	},
]
const Pagination = (props: any) => {
	useEffect(() => {}, [props.isActive.isActive])

	const contentPageList = list.map(item => (
		<li key={item.lol}>
			<PaginationItem active={item.lol === props.isActive.isActive} />
		</li>
	))

	return (
		<nav aria-label='Page navigation example'>
			<ul className='inline-flex items-center -space-x-px mb-[-10px]'>{contentPageList}</ul>
		</nav>
	)
}

export default Pagination
