import { forwardRef, useEffect, useImperativeHandle, useRef, useState } from 'react'

interface OfferMobileItemProps {
	id: string
	customClickEvent: (event: any) => void
	isActive: boolean
	img: string
	background: string
	title: string
	text: string
	setActiveItem: (item: any) => void
}

const OfferMobileItem = forwardRef<HTMLDivElement, OfferMobileItemProps>((props, ref) => {
	const itemRef = useRef<HTMLDivElement>(null)
	const [hasScrolled, setHasScrolled] = useState(false)
	useImperativeHandle(ref, () => itemRef.current!)

	useEffect(() => {
		if (props.isActive && itemRef.current && hasScrolled) {
			const scrollPosition = itemRef.current.offsetLeft - 8
			itemRef.current.parentElement?.scrollTo({ left: scrollPosition, behavior: 'smooth' })
		}
	}, [props.isActive, hasScrolled])

	useEffect(() => {
		const handleScroll = () => {
			setHasScrolled(true)
			window.removeEventListener('scroll', handleScroll)
		}

		window.addEventListener('scroll', handleScroll)

		return () => {
			window.removeEventListener('scroll', handleScroll)
		}
	}, [])

	return (
		<div
			ref={itemRef}
			id={props.id}
			onClick={props.customClickEvent}
			className={`relative inline-block mx-2 bg-gray-100 rounded-lg transition-colors w-44 h-40 
			${
				props.isActive ? 'bg-primary-orange' : 'hover:bg-offer-hov'
			}`}>
			<div className='relative flex flex-col justify-center items-center text-center w-44 h-40'>
				<img
					src={props.img}
					alt='svg'
					className=''
				/>
				<p className='text-base font-semibold mt-2 whitespace-normal'>{props.title}</p>
				<img
					src={props.background}
					alt='svg'
					className='absolute right-0 bottom-0'
				/>
			</div>
		</div>
	)
})

export default OfferMobileItem
