import React, { useState, useEffect, useRef } from 'react'
import classes from './System.module.css'
// import classes from './Systems2/System2.module.css'

const activeClasses = `w-[20.5rem]  bg-green-900 text-gray-50`
const inActiveClasses = `w-[19.5rem]  bg-green-50 text-gray-900 ml-2`

const SystemItemMobile = (props: any) => {
	const [isActive, setIsActive] = useState(true)
	const { id, num, title, content, img, scrollLimitMobile } = props.item

	useEffect(() => {
        const handleScroll = () => {
            console.log('scroll', window.scrollY)
			if (window.scrollY > scrollLimitMobile) {
				setIsActive(false)
			} else {
				setIsActive(true)
			}
		}
		window.addEventListener('scroll', handleScroll)
		handleScroll()

		return () => {
			window.removeEventListener('scroll', handleScroll)
		}
	}, [scrollLimitMobile])

	const ref = useRef(null)

	return (
		<div
			id={`${classes[`card_${id}`]}`}
			ref={ref}
			className={`${classes.card} sticky top-0`}>
			<div
				className={`flex flex-col p-4 rounded-xl border-[2px] border-gray-50 border-solid ${
					isActive ? activeClasses : inActiveClasses
				} transition-all ease-in duration-300`}>
				<div className=''>
					<div className='flex justify-between'>
						<p className={`text-xl font-semibold leading-normal`}>{title}</p>
						<p className={`text-xl font-semibold leading-normal`}>{num}</p>
					</div>
					<div className='py-6 flex justify-center items-center'>
						<img
							src={img}
							alt=''
							className='h-[100px] w-[184px]'
						/>
					</div>
					<div className='text-gray-200 xl:mt-6'>
						<p className='text-sm font-normal leading-normal'>{content}</p>
					</div>
				</div>
			</div>
		</div>
	)
}

export default SystemItemMobile
