import { useState } from 'react'
import { useEffect } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { heroActions } from '../../store/hero-slice'

import PrimaryButton from '../buttonComponent/PrimaryButton'
import PrimaryButtonNav from '../buttonComponent/PrimaryButtonNav'
import logo from '../../img/pages/LOGO.png'

const Navigation = () => {
	const [isMenuHidden, setIsMenuHidden] = useState('hidden')

	const dispatch = useDispatch()
	const handleClickCard = (cardName: string) => {
		switch (cardName) {
			case 'plants':
				dispatch(heroActions.clickCards({ type: 'plants', nav: '/ZZiR' }))
				break
			case 'roofs':
				dispatch(heroActions.clickCards({ type: 'roofs', nav: '/ZZiR/ZieloneDachy' }))
				break
		}
		window.scrollTo({ top: 0 })
		// window.scrollTo({ top: 0 })
	}

	const location = useLocation()
	const windowWidth = window.innerWidth
	const isWider = windowWidth < 756 ? 30 : 50
	const [scroll, setScroll] = useState(false)
	useEffect(() => {
		window.addEventListener('scroll', () => {
			setScroll(window.scrollY >= isWider)
		})
	}, [isWider])

	const handleMenu = (isLogo: boolean) => {
		if (isLogo) setIsMenuHidden('hidden')
		else isMenuHidden === 'hidden' ? setIsMenuHidden('') : setIsMenuHidden('hidden')
	}

	return (
		<nav
		id='navbar'
		className={`bg-system-roof fixed z-[999] top-0 left-0 border border-transparent w-screen`}>
			<div className='max-w-screen-xl flex max-lg:flex-col lg:flex-wrap lg:items-center lg:justify-between lg:mx-auto p-2 max-lg:py-4 xl:p-4 px-0 xl:px-10'>
				<Link
					to='/ZZiR'
					onClick={() => {
						handleClickCard('plants')
						handleMenu(true)
					}}
					className='ml-4 lg:ml-0 mr-auto lg:flex lg:items-center max-lg:flex max-lg:flex-row'>
					<img src={logo} alt="logo" />
					<span className='self-center text-2xl font-bold whitespace-nowrap text-green-900'>Zakad Zieleni</span>
				</Link>

				<button
					onClick={() => handleMenu(false)}
					data-collapse-toggle='navbar-sticky'
					type='button'
					className='absolute inset-y-0 right-4 inline-flex items-center  py-4 text-sm h-max text-green-900 rounded-lg lg:hidden focus:outline-none focus:ring-2 focus:ring-gray-200'
					aria-controls='navbar-sticky'
					aria-expanded='true'>
					<span className='sr-only'>Open main menu</span>
					<svg
						className='w-6 h-6'
						aria-hidden='true'
						fill='currentColor'
						viewBox='0 0 20 20'
						xmlns='http://www.w3.org/2000/svg'>
						<path
							fillRule='evenodd'
							d='M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z'
							clipRule='evenodd'></path>
					</svg>
				</button>

				<div
					className={`lg:items-center lg:justify-between lg:flex lg:w-auto ${isMenuHidden}`}
					id='navbar-sticky'>
					<ul className='flex flex-col lg:p-0 mt-4 m-auto font-medium lg:flex-row lg:space-x-3 lg:mt-0 lg:border-0'>
						<li>
							<Link
								to='/ZZiR'
								onClick={() => {
									handleClickCard('plants')
									handleMenu(false)
								}}
								className={`lg:nav-underline lg:text-base lg:font-medium block py-2 lg:py-2 px-4 lg:px-4 text-green-700 bg-transparent p-0 lg:active:font-semibold lg:active:rounded lg:active:bg-gray-200/90 lg:hover:underline max-lg:border-b-2 ${
									location.pathname === '/ZZiR' ? 'underline' : ''
								}`}>
								Gwna
							</Link>
						</li>
						<li>
							<Link
								to='/ZZiR/ZieloneDachy'
								onClick={() => {
									handleClickCard('roofs')
									handleMenu(false)
								}}
								className={`lg:nav-underline lg:text-base lg:font-medium block py-2 lg:py-2 px-4 lg:px-4 text-green-700 bg-transparent p-0 lg:active:font-semibold lg:active:rounded lg:active:bg-gray-200/90 lg:hover:underline max-lg:border-b-2 ${
									location.pathname === '/ZZiR/ZieloneDachy' ? 'underline' : ''
								}`}
								aria-current='page'>
								Zielone dachy
							</Link>
						</li>
						<li>
							<Link
								to='/ZZiR/Onas'
								className={`lg:nav-underline lg:text-base lg:font-medium block py-2 lg:py-2 px-4 lg:px-4 text-green-700 bg-transparent p-0 lg:active:font-semibold lg:active:rounded lg:active:bg-gray-200/90 lg:hover:underline max-lg:border-b-2 ${
									location.pathname === '/ZZiR/Onas' ? 'underline' : ''
								}`}
								onClick={() => {
									window.scrollTo({ top: 0 })
									handleMenu(false)
								}}>
								O nas
							</Link>
						</li>
						<li>
							<Link
								to='/ZZiR/Galeria'
								className={`lg:nav-underline lg:text-base lg:font-medium block py-2 lg:py-2 px-4 lg:px-4 text-green-700 bg-transparent p-0 lg:active:font-semibold lg:active:rounded lg:active:bg-gray-200/90 lg:hover:underline max-lg:border-b-2 ${
									location.pathname === '/ZZiR/Galeria' ? 'underline' : ''
								}`}
								onClick={() => {
									window.scrollTo({ top: 0 })
									handleMenu(false)
								}}>
								Galeria
							</Link>
						</li>
						<li>
							<Link
								to='/ZZiR/Kontakt'
								className={`lg:nav-underline lg:text-base lg:font-medium block py-2 lg:py-2 px-4 lg:px-4 text-green-700 bg-transparent p-0 lg:active:font-semibold lg:active:rounded lg:active:bg-gray-200/90 lg:hover:underline max-lg:border-b-2 ${
									location.pathname === '/ZZiR/Kontakt' ? 'underline' : ''
								}`}
								onClick={() => {
									window.scrollTo({ top: 0 })
									handleMenu(false)
								}}>
								Kontakt
							</Link>
						</li>
						<li>
							<div className='flex justify-center items-center  lg:hidden'>
								<PrimaryButtonNav />
							</div>
						</li>
					</ul>
				</div>
				<div className='hidden lg:flex'>
					<PrimaryButton />
				</div>
			</div>
		</nav>
	)
}

export default Navigation
