import { ReactNode } from 'react'
import FadeInUpHook from '../../hooks/FadeInUpHook'

interface SectionCmpProps {
	children: ReactNode
	text?: string
}

const SectionWideCmp: React.FC<SectionCmpProps> = ({ children, text }) =>{
	const { ref, fadeInUpAnimation } = FadeInUpHook()
	return (
		<div ref={ref} className={`py-5 xl:py-16 relative z-10 ${text ? 'bg-green-900' : 'bg-custom-light'} ${fadeInUpAnimation}`}>
			<div className='container mx-auto max-w-screen-xl '>
				{children}
			</div>
		</div>
	)
}

export default SectionWideCmp
