const CheckImg = (param: any) => {
	switch (param) {
		case 1:
			return 'bg-[url("/gallery/gallery-1.jpeg")]'
		case 2:
			return 'bg-[url("/gallery/gallery-2.jpeg")]'
		case 3:
			return 'bg-[url("/gallery/gallery-3.jpeg")]'
		case 4:
			return 'bg-[url("/gallery/gallery-4.jpeg")]'
		case 5:
			return 'bg-[url("/gallery/gallery-5.jpeg")]'
		default:
			return 'error'
	}
}
const GalleryMobileItem = (props: any) => {
	const image: string = CheckImg(props.img)

	return (
		<div className='relative inline-block w-80 m-2 h-80 bg-slate-500 rounded-2xl overflow-hidden'>
			<div className={`h-full w-full ${image} bg-center`}>
				<div className='absolute p-4 h-full w-full text-gray-100 flex flex-col justify-end bg-gradient-to-t from-gray-900/70 to-gray-900/0 transition-all hover:delay-75 duration-0 hover:duration-300'>
					<p className='text-base font-semibold'>TITLE</p>
				</div>
			</div>
		</div>
	)
}

export default GalleryMobileItem
