import React from 'react'

//Main icons
import { ReactComponent as bug } from '../../icons/default/bug.svg'
import { ReactComponent as home } from '../../icons/default/home-smile.svg'
import { ReactComponent as masonry } from '../../icons/default/masonry.svg'
import { ReactComponent as menu } from '../../icons/default/menu.svg'
import { ReactComponent as microsoft } from '../../icons/default/microsoft.svg'
import { ReactComponent as minus } from '../../icons/default/minus.svg'
import { ReactComponent as pen } from '../../icons/default/pen.svg'
import { ReactComponent as plant } from '../../icons/default/plant.svg'
import { ReactComponent as plus } from '../../icons/default/plus.svg'
import { ReactComponent as ruler } from '../../icons/default/ruler.svg'
import { ReactComponent as screenshot } from '../../icons/default/screenshot-line.svg'
import { ReactComponent as seeding } from '../../icons/default/seeding.svg'
import { ReactComponent as shape } from '../../icons/default/shape.svg'

//Contact icons
import { ReactComponent as phone } from '../../icons/contactIcons/contacts-book.svg'
import { ReactComponent as mail } from '../../icons/contactIcons/mail.svg'
import { ReactComponent as map } from '../../icons/contactIcons/map-pin.svg'

//Gallery arrow
import { ReactComponent as arrowLeft } from '../../icons/default/arrowLeft.svg'
import { ReactComponent as arrowRight } from '../../icons/default/arrowRight.svg'

//LOGO
import { ReactComponent as logo } from '../../icons/default/LOGO.svg'


const desktopIcon = { height: '72px', width: '72px', fill: '#057A55' }
const mobileIcon = { height: '36px', width: '36px', fill: '#057A55' }

const IconFAQ = { height: '24px', width: '24px', fill: '#014737' }//GREEN-900

const desktopIconContact = { height: '24px', width: '24px', fill: '#057A55' }
const mobileIconContact = { height: '16px', width: '16px', fill: '#057A55' }
const buttonIcon = { height: '20px', width: '20px', fill: '#057A55' }

const galleryIcon = { height: '24px', width: '16px', fill: '#F9FAFB' }
const galleryCloseIcon = { height: '36px', width: '36px', fill: '#F9FAFB' }

const iconSet = [
	{ name: 'bug', icon: bug },
	{ name: 'home', icon: home },
	{ name: 'masonry', icon: masonry },
	{ name: 'menu', icon: menu },
	{ name: 'microsoft', icon: microsoft },
	{ name: 'minus', icon: minus },
	{ name: 'pen', icon: pen },
	{ name: 'plant', icon: plant },
	{ name: 'plus', icon: plus },
	{ name: 'ruler', icon: ruler },
	{ name: 'screenshot', icon: screenshot },
	{ name: 'seeding', icon: seeding },
	{ name: 'shape', icon: shape },
	{ name: 'phone', icon: phone },
	{ name: 'mail', icon: mail },
	{ name: 'map', icon: map },
	{ name: 'arrowLeft', icon: arrowLeft},
	{name: 'arrowRight', icon: arrowRight},
	{name: 'logo', icon: logo}
]
const SvgCmp = (props: { svgName: string; type: string; styles?: string}) => {
	const { svgName, type, styles } = props
	const Icon = iconSet.find(icon => icon.name === svgName)?.icon

	if (!Icon) return null
	else if(type === 'button'){
		return <Icon style={buttonIcon} className='mt-[2px] mr-2' />
	}
	else if(type === 'faq'){
		return <Icon style={IconFAQ} className={`${styles !== undefined ? styles : ''}`} />
	}
	else if(type === 'gallery')
	{
		return <Icon style={galleryIcon} className={`${styles !== undefined ? styles : ''}`} />
	}
	else if(type === 'galleryClose')
	{
		return <Icon style={galleryCloseIcon} className={`${styles !== undefined ? styles : ''}`} />
	}
	else if(type === 'logo')
	{
		return <Icon style={desktopIcon} className={`${styles !== undefined ? styles : ''}`} />
	}
	else if (window.innerWidth > 1024) {
		if (type === 'contact') 
            return <Icon style={desktopIconContact} />
		else 
            return <Icon style={desktopIcon} />
	} else 
    {
        if (type === 'contact') 
            return <Icon style={mobileIconContact} />
	    else 
            return <Icon style={mobileIcon} />}
}

export default SvgCmp
