
import { createBrowserRouter, RouterProvider } from 'react-router-dom'

import React from 'react'
import './styles/App.css'

//MAIN PAGES
import ZakladZieleni from './pages/ZakladZieleni/_Page_ZakladZieleni/ZakladZieleni'
import ZieloneDachy from './pages/ZieloneDachy/_Page_ZieloneDachy/ZieloneDachy'
import Onas from './pages/Onas/Onas'
import ErrorPage from './components/errorPage/ErrorPage'
import Root from './pages/Root'
import Galeria from './pages/Galeria/Galeria'
import Kontakt from './pages/Kontakt/Kontakt'



const router = createBrowserRouter([
	{
	  path: '/ZZiR',
	  element: <Root />,
	  errorElement: <ErrorPage />,
	  children: [
		{ path: '/ZZiR', element: <ZakladZieleni />},
		{ path: '/ZZiR/ZieloneDachy', element: <ZieloneDachy /> },
		{ path: '/ZZiR/Onas', element: <Onas /> },
		{ path: '/ZZiR/Galeria', element: <Galeria /> },
		{ path: '/ZZiR/Kontakt', element: <Kontakt /> },
	  ],
	},
  ])


const App = () => {
	return (
		<RouterProvider router={router}/>
	)
}

export default App


