import React, { useState } from 'react'

type RecurringPriceInterval = 'intensive' | 'ekstensive'

const SystemSwitch = () => {
	const [interval, setInterval] = useState<RecurringPriceInterval>('intensive')

	return (
		<div className='relative w-[317px] h-[34px] flex p-1 rounded-lg bg-green-900/5'>
			<div
				className={`absolute w-[155px] h-[26px] transition-transform duration-300 ease-in-out rounded-lg bg-green-900 transform ${
					interval === 'ekstensive' ? 'translate-x-full' : ''
				}`}
			/>
			<button
				onClick={() => setInterval('intensive')}
				className={`relative flex-grow text-center rounded-lg transition duration-300 ease-in-out z-10 w-full ${
					interval === 'intensive' ? ' text-white' : 'bg-transparent text-gray-900'
				}`}>
				<p className={`text-sm ${interval === 'intensive' ? 'font-semibold': 'font-normal'} leading-none`}>Dachy intensywne</p>
			</button>
			<button
				onClick={() => setInterval('ekstensive')}
				className={`text-sm font-normal leading-none relative flex-grow text-center rounded-lg transition duration-300 ease-in-out z-10 w-full ${
					interval === 'ekstensive' ? ' text-white' : 'bg-transparent text-gray-900'
				}`}>
				<p className={`text-sm ${interval === 'ekstensive' ? 'font-semibold': 'font-normal'} leading-none`}>Dachy ekstensywne</p>
			</button>
		</div>
	)
}

export default SystemSwitch
