import {  Link, useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { heroActions } from '../../../../store/hero-slice'

import Badge from '../../../../components/badgeComponent/Badge'
import img from '../../../../img/pages/ZakladZieleni/GreenRoof.jpeg'
import Elipse from '../../../../components/elipse/Elipse'

const GreeenRoofSection = () => {
	const dispatch = useDispatch()
	const heroState = useSelector((state: any) => state.hero)
	const navigate = useNavigate()

	const handleClick = () => {
				dispatch(heroActions.clickCards({ type: 'roofs', nav: '/ZZiR/ZieloneDachy' }))
				navigate('/ZZiR/ZieloneDachy')
				window.scrollTo({ top: 0 })
	}

	return (
		<div className='mx-4 lg:mx-10 relative z-10'>
			{window.innerWidth > 1024 && <Elipse num={5} classes='top-[0px] right-[-755px]'/>}
			<div className='grid grid-cols-12'>
				<div className='col-span-12 lg:col-span-12 grid grid-cols-12'>
					<div className='col-span-6 lg:col-span-10'>
						<Badge title='Zielone dachy' />
					</div>
					<div className='col-span-6 lg:col-span-2 text-right self-center relative'>
						<Link
							to='/ZZiR/ZieloneDachy'
							// onClick={() => window.scrollTo({ top: 0 })}
							onClick={handleClick}
							className='text-xs text-green-700 mr-4 lg:mr-9'>
							<span className='underline'>Czytaj wicej</span> 
						</Link>
					</div>
					<div className='col-span-12'>
						<p className='text-base lg:text-2xl mt-2 font-semibold'>
							Wsppraca z Sempergreen Group  sprzeda mat rozchodnikowych na terenie caej Polski
						</p>
					</div>
				</div>
			</div>
			<div className='grid grid-cols-12 mt-2 lg:mt-9'>
				<div className='col-span-12 lg:col-span-7 max-md:order-last'>
					<p className='mt-8 lg:mt-0 ml-0 text-xl lg:text-4xl font-medium text-gray-800 leading-normal lg:leading-normal'>
						Jestemy prnym dystrybutorem mat rozchodnikowych firmy Sempergreen, jednego z najwikszych producentw w
						Europie, z ponad 20 letnim dowiadczeniem na rynku zielonych dachw.
					</p>
				</div>
				<div className='col-span-12 lg:col-span-5 flex'>
					<img
					className='max-md:mt-4 xl:my-2 ml-auto w-full xl:w-[411px] h-[160px] xl:h-[290px] object-cover rounded-xl xl:rounded-3xl'
						src={img}
						alt='Rnorodne zielone roliny'
					/>
				</div>
			</div>
		</div>
	)
}

export default GreeenRoofSection
