import SvgCmp from '../../../components/svgComponent/SvgCmp'

const CoontactCard = (props: any) => {
	const { icon, title, text, text2 } = props

	const href = icon !== 'map'?  icon === 'phone' ? 'tel:502-554-410' : 'mailto:biuro@zielenizaklad.pl' : ''
	const href2 = icon !== 'map' ? 'tel:507-046-756' : ''
	return (
		<div className='p-4 border border-border-contact rounded-xl relative z-10'>
			<SvgCmp
				svgName={icon}
				type='contact'
			/>
			<p className='text-base xl:text-2xl font-semibold mt-2'>{title}</p>
			<p className='text-sm xl:text-base font-normal mt-2 text-gray-800'>
				<a href={href} className='hover:underline'>{text}</a>
			</p>
			{text2 && (
				<p className='text-sm xl:text-base font-normal mt-2 text-gray-800'>
					<a href={href2} className='hover:underline'>{text2}</a>
				</p>
			)}
		</div>
	)
}

export default CoontactCard
