import { createSlice, PayloadAction } from '@reduxjs/toolkit'

const initialState = {
	type: 'plants',
	isPlantsActive: true,
	isRoofsActive: false,
	nav: '/',
	carouselData: {
		showFullScreen: false,
		img: '', // or any initial value you want
	} as { showFullScreen: boolean; img: string } | null,
}

const heroSlice = createSlice({
	name: 'hero',
	initialState: initialState,
	reducers: {
		clickCards: (state, action: PayloadAction<any>) => {
			switch (action.payload.type) {
				case 'plants':
					return {
						...state,
						isPlantsActive: true,
						isRoofsActive: false,
						type: action.payload.type,
						nav: '/',
					}
				case 'roofs':
					return {
						...state,
						isPlantsActive: false,
						isRoofsActive: true,
						type: action.payload.type,
						nav: '/ZieloneDachy',
					}
				default:
					break
			}
		},
		clickPlants(state, action: PayloadAction<any>) {
			state.isPlantsActive = action.payload
			state.isRoofsActive = !action.payload
			state.type = 'plants'
		},
		clickRoofs(state, action: PayloadAction<any>) {
			state.isRoofsActive = action.payload
			state.isPlantsActive = !action.payload
			state.type = 'roofs'
		},
		updateCarouselData: (
			state,
			action: PayloadAction<{showFullScreen: boolean; img: string }>
		) => {
			state.carouselData = action.payload
		},
	},
})

export const heroActions = heroSlice.actions
export default heroSlice.reducer
