import { useState, useRef, useEffect } from 'react';

const FadeInUpHook = (props?: string) => {
  const [isVisible, setIsVisible] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if(props === 'footer')
          setIsVisible(entry.isIntersecting);
        else
          entry.isIntersecting && setIsVisible(true);
      },
      {
        root: null,
        rootMargin: '0px',
        threshold: 0.1,
      }
    );

    if (ref.current) {
      observer.observe(ref.current);
    }

    return () => {
      if (ref.current) {
        observer.unobserve(ref.current);
      }
    };
  }, []);

  const fadeInUpAnimation = isVisible ? 'opacity-1 duration-400 translate-y-0' : 'opacity-0 translate-y-10';

  return { ref, fadeInUpAnimation };
}

export default FadeInUpHook;