import React, { useState, useEffect, useRef } from 'react'
import classes from './System.module.css'

const activeClasses = `xl:rounded-2xl w-[20.5rem] xl:w-[50.75rem] border-3 xl:border-4 xl:border-gray-50 xl:border-solid bg-green-900 text-gray-50`
const inActiveClasses = `xl:rounded-3xl xl:w-[781px] xl:ml-4 border-3 xl:border-4 xl:border-gray-50 xl:border-solid bg-green-50 text-gray-900`

const SystemItem = (props: any) => {
	const [isActive, setIsActive] = useState(true)
	const { id, num, title, content, img, scrollLimitDesktop, scrollLimitDesktopScale, scrollLimitDesktopMac } = props.item

	useEffect(() => {
		const handleScroll = () => {
			if (window.innerWidth > 1512) {
				if (window.scrollY > scrollLimitDesktop) {
					setIsActive(false)
				} else {
					setIsActive(true)
				}
			} else if (window.innerWidth > 1280) {
				if (window.scrollY > scrollLimitDesktopMac) {
					setIsActive(false)
				} else {
					setIsActive(true)
				}
			}else{
				if (window.scrollY > scrollLimitDesktopScale) {
					setIsActive(false)
				} else {
					setIsActive(true)
				}
			}
		}
		window.addEventListener('scroll', handleScroll)
		handleScroll()

		return () => {
			window.removeEventListener('scroll', handleScroll)
		}
	}, [scrollLimitDesktop, scrollLimitDesktopScale, scrollLimitDesktopMac])

	const ref = useRef(null)

	const useIsVisible = (ref: any) => {
		const [isIntersecting, setIntersecting] = useState(false)

		useEffect(() => {
			const observer = new IntersectionObserver(([entry]) => setIntersecting(entry.isIntersecting))
			observer.observe(ref.current)
			return () => {
				observer.disconnect()
			}
		}, [ref])
		return isIntersecting
	}

	const isVisible = useIsVisible(ref)

	return (
		<div
			id={`${classes[`card_${id}`]}`}
			ref={ref}
			className={`${classes.card} sticky top-0`}>
			<div
				className={`flex xl:gap-6 p-4 xl:p-6 rounded-xl ${
					isActive ? activeClasses : inActiveClasses
				} transition-all ease-in duration-300`}>
				<div className='xl:basis-1/4'>
					<img
						src={img}
						alt=''
						className='w-[200px] h-full'
					/>
				</div>
				<div className='xl:basis-3/4'>
					<div className='flex justify-between'>
						<p className={`xl:text-3xl xl:font-semibold xl:leading-normal`}>{title}</p>
						<p className={`xl:text-3xl xl:font-semibold xl:leading-normal`}>{num}</p>
					</div>
					<div className='xl:text-gray-200 xl:mt-6'>
						<p className='xl:text-xl xl:font-normal xl:leading-normal'>{content}</p>
					</div>
				</div>
			</div>
		</div>
	)
}

export default SystemItem
