import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'

import GalleryItem from './GalleryItem'
import GalleryMobile from './GalleryMobile'
import GalleryMobileItem from './GalleryMobileItem'

import img from '../../../../styles/gallery/gallery-1.jpeg'
import img2 from '../../../../styles/gallery/gallery-2.jpeg'
import img3 from '../../../../styles/gallery/gallery-3.jpeg'
import img4 from '../../../../styles/gallery/gallery-4.jpeg'
import img5 from '../../../../styles/gallery/gallery-5.jpeg'
import Elipse from '../../../../components/elipse/Elipse'

const getWindowSize = () => {
	const { innerWidth, innerHeight } = window
	return { innerWidth, innerHeight }
}

const gallery = [
	{
		id: 1,
		img: img,
		name: 'first',
		alt: 'zielone',
	},
	{
		id: 2,
		img: img2,
		name: 'second',
		alt: 'zielone',
	},
	{
		id: 3,
		img: img3,
		name: 'third',
		alt: 'zielone',
	},
	{
		id: 4,
		img: img4,
		name: 'fourth',
		alt: 'zielone',
	},
	{
		id: 5,
		img: img5,
		name: 'fifth',
		alt: 'zielone',
	},
]
let contentGallery
let contentGalleryMobile
const Gallery = (props: any) => {
	const [windowSize, setWindowSize] = useState(getWindowSize())
	useEffect(() => {
		function handleWindowResize() {
			setWindowSize(getWindowSize())
		}

		window.addEventListener('resize', handleWindowResize)

		return () => {
			window.removeEventListener('resize', handleWindowResize)
		}
	}, [])

	contentGallery = []
	contentGalleryMobile = []
	for (let i = 0; i < gallery.length; i++) {
		contentGallery.push(
			<GalleryItem
				img={gallery[i].id}
				alt={gallery[i].alt}
				name={gallery[i].name}
				key={i}
			/>
		)
	}
	for (let i = 0; i < gallery.length; i++) {
		contentGalleryMobile.push(
			<GalleryMobileItem
				img={gallery[i].id}
				alt={gallery[i].alt}
				name={gallery[i].name}
				key={i}
			/>
		)
	}

	const galleryDesktop = <div className='mt-6 lg:mt-12 flex justify-between gap-4 max-md:reltive'>{contentGallery}</div>

	const galleryMobile = <GalleryMobile gallery={contentGalleryMobile} />

	return (
		<div className='mx-0 lg:mx-10 relative z-10'>
			{window.innerWidth > 1024 && (
				<Elipse
					num={4}
					classes={`${props.type !== 'roof' ? 'top-[79px]' : 'top-[190px]'} left-[-430px]`}
				/>
			)}

			<div className='flex flex-row justify-between'>
				<p className='max-md:mx-4 text-base lg:text-2xl font-semibold'>Nasze realizacje</p>
				<Link
					to='/ZZiR/Galeria'
					onClick={() => window.scrollTo({ top: 0 })}
					className='text-xs text-green-700 mr-3 lg:mr-9 self-center'>
					<span className='underline'>Zobacz wszystkie</span> 
				</Link>
			</div>
			<div className='mt-2 lg:mt-2 flex justify-between gap-4 max-md:reltive'></div>
			{windowSize.innerWidth > 768 ? galleryDesktop : galleryMobile}
		</div>
	)
}

export default Gallery
